/* MapComponent.css */

/*    */
.google-map-container {
    height: 50vh; /*   50%  */
    width: 100%; /*     */
    max-height: 400px; /*    */
    border: 1px solid #ccc; /*   */
    border-radius: 8px; /*   */
    box-shadow: 0 2px 6px rgba(0,0,0,0.3); /*   */
    position: relative; /* z-index     */
    z-index: 0; /*     */
}

/*     */
.autocomplete-input {
    box-sizing: border-box; /*     */
    width: calc(50% - 24px); /*        */
    height: 40px; /*    */
    padding: 8px 12px; /*   */
    border: 2px solid #ddd; /*   */
    border-radius: 4px; /*   */
    margin: 10px auto; /*      */
    display: block; /*     */
    font-size: 16px; /*   */
    outline: none; /*    */
    position: absolute;
    z-index: 1000;
}

/*       */
.autocomplete-input:focus {
    border-color: #0056b3; /*     */
    box-shadow: 0 0 8px rgba(0,86,179,0.8); /*    */
}

/*    */
.submit-button {
    padding: 8px 16px;
    background-color: #4CAF50; /*   */
    color: white; /*   */
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 10px;
}

.submit-button:hover {
    background-color: #45a049; /*     */
}
.autocomplete-input {
    position: absolute; /*     */
    top: 10px; /*  10px   */
    left: 50%; /*   */
    transform: translateX(-50%); /*      */
    width: 300px; /*   */
    height: 40px; /*   */
    padding: 8px; /*   */
    font-size: 16px; /*    */
    box-shadow: 0 2px 6px rgba(0,0,0,0.3); /*    */
    border: none; /*   */
    border-radius: 5px; /*   */
    z-index: 1000; /*     */
}
