.thirdSectionContainer {
    position: relative;
    width: 100%;
    height: 100vh; /*     */
    overflow: hidden; /*      */
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    background: black; /*     */
}

.thirdSectionContent {
    width: 90%; /*       */
    max-width: 1200px; /*    */
    height: auto;
    margin: auto;
    text-align: center;
    overflow: hidden;
}

/* .logo  */
.thirdSectionContainer .logo {
    color: white;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: 4px;
}

/* .logo  span  */
.thirdSectionContainer .logo span {
    font-size: 12px;
    color: #f39041;
    display: block;
}

/*   */
.thirdSectionButton {
    position: absolute;
    bottom: 20px; /*     */
    left: 50%;
    transform: translateX(-50%);
}
