/* src/main2/WeatherPage.module.css */

html, body {
    height: 100%;
    margin: 0;
}

.bg {
    position: relative;
    background-image: url("https://images6.alphacoders.com/339/339532.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 60px;
    width: 100%;
    overflow: hidden;
}

/*   */
.bg::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0.9) 0%,         /*     */
            rgba(0, 0, 0, 0.4) 40%,        /*    */
            rgba(0, 0, 0, 0.4) 60%,        /*    */
            rgba(0, 0, 0, 0.9) 100%        /*     */
    );
    z-index: 1;
}

/*       z-index  */
.textCenter {
    font-size: 56px;
    color: white;
    text-align: center;
    margin-bottom: 20px;
    z-index: 2; /*     */
}
